* {
  margin: 0;
  padding: 0;
  outline: 0;
  box-sizing: border-box;
}

html, body {
  height: 100%;
}

#root {
  min-height: 100%;
  position: relative;
}

body {
  background: #434a56;
}

body, input, button {
  font-family: 'Trebuchet MS', 'Lucida Sans', Arial, sans-serif;
}